<template>
  <div class="col-md-6 col-lg-4">
    <div class="card c-card c-card-shadow my-3 position-relative" @click="checkIntroduction(item.id)" >
      <div :style="{backgroundImage: `url(${item.imageUrl})`}" class="card-img">
      </div>
      <div class="card-body p-2 p-md-3">
        <p class="font-weight-bold card-text">{{ item.title }}</p>
      </div>
      <div class="card-overlay">
        <button class="card-more btn btn-outline-warning">影片介紹</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CardTV',
  props: { item: Object },
  methods: {
    checkIntroduction(id) {
      this.$router.push({ name: 'Product', params: { productId: id } });
    },
  },
};
</script>
